.app_container{
    position: relative;
}
.call_contact{
    position: fixed;
    z-index: 9999;
    bottom:0;
    right:0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 160px;
    height: 56px;
    background-color: #c7eef5;
    cursor: pointer;
    padding: 0 22px 0 18px;
    margin-bottom: 24px;
    margin-right: 24px;
}
.contact_icon{
    padding-right: 10px;
}